import Vue from "vue";
import Router from "vue-router";
import VideoView from "./views/VideoView";
import PhotoView from "./views/PhotoView";
import GifView from "./views/GifView";
import PushView from "./views/PushView"


Vue.use(Router);

export default new Router({
  routes: [
    {
      path: "/",
      name: "home",
      component: VideoView
    },
    {
      path: "/publish",
      name: "publish",
      component:PushView
    },
    {
      path: "/photo",
      name: "photo",
      component:PhotoView
    },
    {
      path: "/gif",
      name: "gif",
      component:GifView
    }
  ]
});
