<template>
  <List :title="fileType" :items="items" @click="(data, event)=>$emit('click', data, event)" />
</template>

<script>

import List from "../components/List";
import zeroUp from "../lib/ZeroUp";
export default {
  props:{
    fileType:{
      type: String,
      default: ''
    }
  },
  components: {
    List
  },
  data() {
    return {
      items: []
    };
  },

  created() {
    zeroUp.listFiles(this.fileType).then(list => {
      console.log(list)
      this.items = list;
    });
  }
}

