<template>
  <v-layout row>
    <v-flex xs12 sm6 offset-sm3>
      <v-toolbar class="hidden-md-and-up" app>
        <v-toolbar-title>{{title}}</v-toolbar-title>
      </v-toolbar>

      <v-form ref="form" v-model="valid" value="false">
        <v-text-field :rules="titleRule" v-model="mTitle" required placeholder="请输入一个霸气的名称"></v-text-field>
        <v-subheader class="px-0">选择文件类型</v-subheader>
        <v-radio-group class="mt-0" v-model="fileType" :mandatory="false">
          <v-radio label="视频" value="video" color="deep-purple"></v-radio>
          <v-radio label="GIF动图" value="gif" color="deep-purple"></v-radio>
          <v-radio label="图片" value="photo" color="deep-purple"></v-radio>
        </v-radio-group>
        <v-divider></v-divider>
        <v-checkbox label="使用外部链接" v-model="showLinkInput"></v-checkbox>
        <v-subheader v-if="!showLinkInput" class="px-0">选择文件</v-subheader>

        <v-list v-if="!showLinkInput">
          <template v-for="(item,i) in fileList">
            <v-list-tile :key="item.filename">
              <v-list-tile-action @click="()=>null" class="hidden-sm-and-down">
                <v-btn @click="remove(i)" icon>
                  <v-icon color="deep-purple">remove_circle_outline</v-icon>
                </v-btn>
              </v-list-tile-action>

              <v-list-tile-content>
                <v-list-tile-title>{{item.filename}}</v-list-tile-title>
              </v-list-tile-content>

              <v-list-tile-action class="hidden-md-and-up">
                <v-btn @click="remove(i)" icon>
                  <v-icon color="deep-purple">remove_circle_outline</v-icon>
                </v-btn>
              </v-list-tile-action>
            </v-list-tile>
            <v-divider :key="i"></v-divider>
          </template>
          <v-list-tile class="file-picker" v-show="'video' !== fileType || fileList.length === 0">
            <v-list-tile-action class="hidden-sm-and-down">
              <v-icon color="deep-purple">add_circle_outline</v-icon>
            </v-list-tile-action>

            <v-list-tile-content>
              <v-list-tile-title>添加文件</v-list-tile-title>
            </v-list-tile-content>

            <v-list-tile-action class="hidden-md-and-up" @click="pickFile">
              <v-icon color="deep-purple">add_circle_outline</v-icon>
            </v-list-tile-action>
            <input class="hidden-sm-and-down" type="file" @change="pickFile">
          </v-list-tile>
          <v-divider></v-divider>
        </v-list>
        <v-text-field v-if="showLinkInput" :rules="linkRule" placeholder="请输入外链地址（例如：http://www.baidu.com/）"></v-text-field>
        <v-btn
          class="upload-btn"
          dark
          block
          @click="submit"
          color="deep-purple"
        >上传</v-btn>
      </v-form>
    </v-flex>
    <v-dialog v-model="dialog" max-width="290">
      <v-card>
        <v-card-text>{{dialogMsg}}</v-card-text>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="deep-purple darken-1" flat="flat" @click="()=>(dialog = false,dialogButton=null)">{{dialogButton||`我知道了`}}</v-btn>
          <v-spacer></v-spacer>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-layout>
</template>
<script>
import { boolMobile } from "../lib/tools";
import ZeroUp from "../lib/ZeroUp";

function checkFileType(uploadType, fileType) {
  const conditions = {
    video: {
      "video/mp4": true,
      "video/webm": true,
      "video/ogg": true
    },

    photo: {
      "image/jpeg": true,
      "image/png": true
    },
    gif: {
      "image/gif": true
    }
  };

  return conditions[uploadType] && conditions[uploadType][fileType];
}

const tips = {
  video: "视频只能上传 mp4、webm、ogg 格式",
  gif: "请选择gif文件",
  photo: "图片只能上传 jpg、png 格式"
};

export default {
  data() {
    return {
      valid: false,
      fileType: "video",
      title: "共享资源",
      // stage: 1,
      mTitle:"",
      fileList: [],
      dialog: false,
      dialogMsg: "",
      showLinkInput: false,
      dialogButton: null,
      titleRule:[
        v => !!v || '名称不能为空',
        v => v&&(v.length >= 4 && v.length <= 30) || '名称的长度必须在4到30个字符之间'
      ],
      linkRule: [
        v=>(/^https?:\/\/[\d\-a-zA-Z]+(\.[\d\-a-zA-Z]+)*\/?$/.test(v) || '请输入一个有效的链接')
      ]
    };
  },

  methods: {
    async submit() {
      if( !this.$refs.form.validate() ) return;
      if( this.fileList.length === 0 ){
        this.dialog = true;
        this.dialogMsg = '请添加要分享的文件';
        return;
      }
      let doneCount = 0;
      let failCount = 0;
      this.dialog = true;
      this.dialogButton = '关闭'
      this.dialogMsg = `总共${this.fileList.length}个文件，完成${doneCount}个文件，失败${failCount}个文件`;

      const postInfo = {
        title:this.mTitle,
        post_type: this.fileType
      }

      const postId = await ZeroUp.addPost(postInfo);

      console.log(postId)

      ZeroUp.uploadFiles({post_id:postId, ...postInfo}, this.fileList, ret => {
        const {success, error} = ret;
        success && doneCount++;
        error && failCount++;
        this.dialogMsg = `共${this.fileList.length}个文件，完成${doneCount}个文件，失败${failCount}个文件`;
      });
    },
    remove(i) {
      // console.log(11);
      this.fileList.splice(i, 1);
    },
    checkFile(file, callback) {
      // console.log(file.size >= 5*1024)
      if(file.size >= 512 * 1024 * 1024 || file.size <= 5*1024 ){
        this.dialog = true;
        this.dialogMsg = '文件的大小必须在5KB到500M之间';
        return;
      }

      if (file && checkFileType(this.fileType, file.type)) {
        callback();
      } else {
        this.dialog = true;
        this.dialogMsg = tips[this.fileType];
        this.pickedFile = null;
      }
    },
    pickFile(e) {
      // console.log('change')
      if (boolMobile) {
        ZeroUp.pickFile().then(fileInfo => {
          this.checkFile(fileInfo, () => {
            this.fileList.push({ ...fileInfo, filename: fileInfo.name });
          });
        });
      } else {
        let file = e.target.files[0];
        this.checkFile(file, () => {
          this.fileList.push({
            filename: file.name,
            blob: file,
            size:file.size
          });
        });
        e.target.value=null;
      }
    }
  },

  // computed: {
  //   showLinkInput() {
  //     return this.fileType === "link";
  //   }
  // },

  watch: {
    fileType() {
      this.fileList = [];
    }
  }
};
</script>

