import ZeroFrame from './ZeroFrame';
import Text from './Text';
import Time from './Time';
import {
  boolMobile
} from "./tools";
// import {pickFile, uploadFile} from './NativeCall';
import * as NativeCall from './NativeCall';

class ZeroUp extends ZeroFrame {
  site_info = null;
  async listFiles(fileType, pageNo = 0, order = 'date_added') {

    this.site_info = await this.cmdp('siteInfo', {});

    const pageSize = 20;
    const post_res = await this.cmdp('dbQuery', [`SELECT * FROM post WHERE post_type='${fileType}' ORDER BY date_added DESC LIMIT ${pageSize*pageNo}, ${pageSize}`]);

    // console.log(`SELECT * FROM post WHERE post_type='${fileType}' ORDER BY date_added DESC LIMIT ${pageSize}, ${pageSize*pageNo}`)
    const files_res = await this.cmdp('dbQuery',
      [`SELECT * FROM file LEFT JOIN json USING(json_id) 
                                      WHERE post_id in (${post_res.map(p=>p.id).join(',')}) 
                                      ORDER BY date_added DESC`]);

    const stat_res = await this.cmdp("optionalFileList", {
      filter: "bigfile",
      limit: 2000,
      // orderby: orderby
    });

    let stats = {}
    for (const stat of stat_res)
      stats[stat.inner_path] = stat;

    const post_map = post_res.reduce((data, val) => {
      // console.log(data);
      data[`${val.json_id}-${val.id}`] = { ...val,
        statics: []
      };
      return data;
    }, {});

    for (const file of files_res) {

      const post = post_map[`${file.json_id}-${file.post_id}`];
      if (post) {

        file.id = file.directory + "_" + file.date_added;
        file.inner_path = `/${this.site_info.address}/data/users/${file.directory}/${file.file_name}`;
        // file.data_inner_path = `data/users/${file.directory}/data.json`;
        // file.content_inner_path = `data/users/${file.directory}/content.json`;
        file.stats = stats[file.inner_path];
        file.stats = file.stats || {};
        file.stats.peer = file.stats.peer || 0;
        file.stats.peer_seed = file.stats.peer_seed || 0;
        file.stats.peer_leech = file.stats.peer_leech || 0;

        post.statics.push(file);
      }
    }
    // console.log(post_map)
    return Object.values(post_map).filter((ele)=>ele.statics.length>0);
  }

  formatFileName(file_name) {
    if (file_name.replace(/[^A-Za-z0-9]/g, "").length < 20) {
      file_name = Time.timestamp() + "-" + file_name;
    }
    return file_name;
  }

  async pickFile() {
    const ret = await NativeCall.pickFile();

    const name = this.formatFileName(ret.name);
    // const size = ret.size;
    // const uri = ret.uri;

    return {
      ...ret,
      name
    }
  }

  async uploadFileNative(postInfo, fileInfo, successFunction, errorFunction) {

    if (!fileInfo) {
      errorFunction(fileInfo);
      throw new Error(`method:uploadFileNative, fileInfo is ${fileInfo}`)
    }

    // const res = 
    try {
      await this.checkContentJson();

      const {
        filename,
        size,
        uri
      } = fileInfo;
      const init_res = await this.cmdp("bigfileUploadInit", ["data/users/" + this.site_info.auth_address + "/" + filename, size]);

      console.log(postInfo)

      await NativeCall.uploadFile(filename, init_res.url, uri);

      console.log('upload done')
      
      await this.registerUpload(postInfo, filename, init_res.relativePath, size, Time.timestamp());
      
      console.log('registerUpload done')

      const ret = await this.cmdp("siteSign", {
        inner_path: "data/users/" + this.site_info.auth_address + "/content.json"
      });

      // await this.cmdp("sitePublish", {
      //   inner_path: `data/users/${this.site_info.auth_address}/content.json`,
      //   "sign": false
      // });

      successFunction(fileInfo);
    } catch (error) {
      errorFunction(fileInfo);
    }
  }

  async addPost(postInfo) {
    const inner_path = `data/users/${this.site_info.auth_address}/data.json`;
    let res = await this.cmdp("fileGet", [inner_path, false]);

    if (res) {
      res = JSON.parse(res);
    }
    if (res == null) {
      res = {
        next_post_id: 0,
        post: []
      };
    }
    if (res.post == null) {
      res.post = [];
      res.next_post_id = 1;
    }

    const newPost = {
      id: res.next_post_id++,
      ...postInfo,
      date_added: Time.timestamp()
    };

    res.post.push(newPost);

    await this.cmdp("fileWrite", [inner_path, Text.fileEncode(res)]);

    return newPost.id;
  }

  /**
   * 写入上传到文件
   * @param {string} title 
   * @param {string} file_name 
   * @param {number} file_size 
   * @param {number} date_added 
   */
  registerUpload(postInfo, file_name, file_size, date_added) {
    const inner_path = `data/users/${this.site_info.auth_address}/data.json`;
    return this.cmdp("fileGet", [inner_path, false])
      .then(res => {
        const {
          post_id,
          title
        } = postInfo;
        if (res) {
          res = JSON.parse(res);
        }
        if (res == null) {
          res = {};
        }
        if (res.file == null) {
          res.file = {};
        }

        console.log('---->', post_id)

        res.file[file_name] = {
          post_id,
          title,
          size: file_size,
          date_added
        };

        return this.cmdp("fileWrite", [inner_path, Text.fileEncode(res)]);
      });
  }

  async uploadFileBrowser(postInfo, file, successFunction, errorFunction) {

    // this.file_info = {};
    // const res = 
    await this.checkContentJson();

    // let file_name = this.formatFileName(file.filename);

    const init_res = await this.cmdp("bigfileUploadInit", [`data/users/${this.site_info.auth_address}/${file.filename}`, file.size]);

    const formdata = new FormData();

    const req = new XMLHttpRequest();
    this.req = req;

    formdata.append(file.filename, file.blob);
    req.upload.addEventListener("loadstart", (progress) => {
      this.log("loadstart", arguments);
      // this.file_info.started = progress.timeStamp;
      // return Page.setPage("uploader");
    });
    req.upload.addEventListener("loadend", () => {
      // this.log("loadend", arguments);
      // this.file_info.status = "done";
      // const postId = `${Time.timestamp()}-${btoa(escape(ostInfo.title)).substr(0, 16)}`;
      this.registerUpload(postInfo, init_res.file_relative_path, file.size, Time.timestamp())
        .then(res => {
          return this.cmdp("siteSign", {
            inner_path: `data/users/${this.site_info.auth_address}/content.json`
          });
        })
        // .then(res => {
        //   return this.cmdp("sitePublish", {
        //     inner_path: `data/users/${this.site_info.auth_address}/content.json`,
        //     "sign": false
        //   });
        // })
        .then(res => {
          return successFunction(res);
        })
    });

    req.addEventListener("error", () => {
      this.log("error", arguments);
      errorFunction(arguments);
    });

    req.withCredentials = true;
    req.open("POST", init_res.url);
    return req.send(formdata);

  }

  handleUploadDone() {

  }

  uploadFiles(postInfo, fileList = [], progressFunction) {

    fileList.forEach(file => {
      const fileName = this.formatFileName(file.filename);

      const func = boolMobile ? this.uploadFileNative : this.uploadFileBrowser;

      func.apply(this, [postInfo, { ...file,
        filename: fileName
      }, (ret) => {
        progressFunction({
          success: file
        });
      }, () => {
        progressFunction({
          error: file
        });
      }]);

    });
  }

  checkContentJson() {

    return new Promise(done => {
      var inner_path = "data/users/" + this.site_info.auth_address + "/content.json";
      this.cmd("fileGet", [inner_path, false], (res) => {
        if (res) {
          res = JSON.parse(res);
        } else {

          res = {};
        }
        var optional_pattern = "(?!data.json)";
        if (res.optional == optional_pattern) return done();

        res.optional = optional_pattern;
        this.cmd("fileWrite", [inner_path, Text.fileEncode(res)], done);
      });
    });

  }
}

export default new ZeroUp();