import querystring from 'querystring';
import { boolMobile } from "./tools";

const scheme = "zshare://com.zshare.app/path";

const global = boolMobile ? window.top : window;

const callbacks = global.__CALLBACKS__ = {};

export function nativeCall(action, params = {}) {

  let callbackName = createCallbackName();

  const promise = new Promise((resolve, reject) => {
    callbacks[callbackName] = (ret) => {
      resolve(ret);
    };

    setTimeout(() => {
      reject();
    }, 15*60000);

  });


  const query = {
    action,
    ...params,
    callback_name: callbackName
  }

  // console.log(`${scheme}?${querystring.stringify(query)}`)

  location.href = `${scheme}?${querystring.stringify(query)}`;

  return promise;
}

global.nativeCallback = function _nativeCallback(strResult) {

  const {callbackName, ret} =  JSON.parse(strResult);

  const _callbackFunction = window.top.__CALLBACKS__[callbackName];

  // console.log('callback', callbackName)
  
  if (_callbackFunction) {
    try {
      // const _ret = JSON.parse(ret);
      // console.log('callback', _ret)
      _callbackFunction(ret);
    } catch (error) {
      _callbackFunction({})
      console.log('native callback', error.message||error)
    }

  }
}

function createCallbackName() {
  return `callback_${Math.random()*1000>>0}${Date.now()}`;
}

export function pickFile() {
  return nativeCall('file_get')
}

export function uploadFile(fileName, uploadUrl, filePath) {
  return nativeCall('file_upload', {
    args: JSON.stringify([fileName, uploadUrl, filePath])
  });
  // "zshare://com.zshare.app/path?action=file_upload&args=" + encodeURIComponent(JSON.stringify([name, init_res.url, uri]));
}