<template>
  <v-container fluid>
    <v-layout justify-center row wrap>
      <v-flex xs12 sm9 lg9>
        <v-container fluid grid-list-lg>
          <v-layout row wrap>
            <v-flex xs12 sm4 lg3 v-for="(ele,i) in items" :key="i">
              <v-card>
                <video
                  v-if="ele.post_type == 'video'"
                  width="100%"
                  height="auto"
                  preload="auto"
                  controls
                  controlsList="nofullscreen nodownload noremoteplayback"
                  no-referrer
                  :src="`${ele.statics[0].inner_path}#t=0.5`"
                  @click="itemClick(ele, $event)"
                ></video>
                <img v-else width="100%" height="auto" no-referrer :src="ele.statics[0].inner_path">
                <v-card-title>
                  <div class="text-truncate">
                    <span>{{ele.title}}</span>
                  </div>
                </v-card-title>
              </v-card>
            </v-flex>
          </v-layout>
        </v-container>
      </v-flex>
    </v-layout>
  </v-container>
</template>
<script>
export default {
  props: {
    title: String,
    items: Array
  },
  methods:{
    itemClick(item, event){
      this.$emit('click', item, event)
    }
  }
};
</script>